.board {
	position: absolute;
	margin: 0;
	height: 100%;
	width: 100%;
	display: grid;
	place-content: center;
	overflow: hidden;
}

.board-carousel {
	position: relative;
	display: grid;
	transform-style: preserve-3d;
	transform: perspective(450px) rotateX(0deg);
}

.board-carousel::before {
	content: "";
	position: absolute;
	inset: -180%;
	clip-path: circle(49%);
	transform: translateZ(40px) rotate(0deg);
	opacity: 1;
	background: transparent;
}