.scoreboard {
	grid-area: 1/1;
	width: 150px;
	aspect-ratio: 1;
	object-fit: cover;
	transform-style: preserve-3d;
	user-select: none;
}

.scoreboard-box {
	transform-style: preserve-3d;
	transform: translateZ(0px);
  position: absolute;
	bottom: 0;
	width: 100%;
	display: flex;
	flex-direction: column;
	padding: 6px;
	background: #1818181c;
	box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 1px, rgba(0, 0, 0, 0.07) 0px 2px 2px, rgba(0, 0, 0, 0.07) 0px 4px 4px, rgba(0, 0, 0, 0.07) 0px 8px 8px, rgba(0, 0, 0, 0.07) 0px 16px 16px;
	border-radius: 10px;
	box-sizing: border-box;
}

.scoreboard-box-line {
	text-align: center;
	font-size: 20px;
	font-weight: 400;
	text-transform: uppercase;
	display: flex;
}

.scoreboard-box-line-place {
	text-align: left;
}

.scoreboard-box-line-name {
	flex: 1;
	text-align: right;
}