.board-carousel-item:nth-child(1) {
	--carousel_item_x_pos: 238px;
	--carousel_item_y_pos: -137px
}

.board-carousel-item:nth-child(2) {
	--carousel_item_x_pos: -238px;
	--carousel_item_y_pos: -137px
}

.board-carousel-item:nth-child(3) {
	--carousel_item_x_pos: 0px;
	--carousel_item_y_pos: 275px
}

.board-carousel-item {
	cursor: pointer;
	grid-area: 1/1;
	width: 150px;
	height: 150px;
	aspect-ratio: 1;
	object-fit: cover;
	transform-style: preserve-3d;
	backface-visibility: hidden;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	user-select: none;
}

.board-carousel-item-scrore {
	max-height: 30px;
	text-align: center;
	font-size: 30px;
	font-weight: 500;
	margin-bottom: 5px;
}

.board-carousel-item-icon {
	width: 120px;
	height: 120px;
	image-rendering: smooth;
}