/*//////////////////////////////////////////////////////////////////
[ FONT ]*/

/* Whitney Book Regular */
@font-face {
	font-family: Whitney;
	font-weight: 400;
	src: url('../fonts/Whitney/whitneyBookRegular.woff') format("woff")
}

/* Whitney Medium Regular */
@font-face {
	font-family: Whitney;
	font-weight: 500;
	src: url('../fonts/Whitney/WhitneyMediumRegular.woff') format("woff")
}

/* Whitney Semibold Regular */
@font-face {
	font-family: Whitney;
	font-weight: 600;
	src: url('../fonts/Whitney/WhitneySemiboldRegular.woff') format("woff")
}

/* Whitney Bold Regular */
@font-face {
	font-family: Whitney;
	font-weight: 700;
	src: url('../fonts/Whitney/WhitneyBoldRegular.woff') format("woff")
}

/*//////////////////////////////////////////////////////////////////
[ ROOT ]*/

:root {
	--background: #000000;
	--scroll-color: #ffffff13;
	--text: #fff;
	--architects: #33C47F;
	--analysts: #4ca3ff;
	--seekers: #D950FF;
}

/*//////////////////////////////////////////////////////////////////
[ MAIN ]*/

#root {
	height: 100%;
}

body,
html {
	background-color: var(--background);
	font-family: "Whitney", sans-serif;
	text-rendering: optimizeLegibility;
	font-size: 16px;
	/* Position */
	height: 100vh;
	margin: 0;
	padding: 0;
	overflow: hidden;
}

::-webkit-input-placeholder,
body,
button,
input,
select,
textarea {
	font-family: "Whitney", sans-serif;
	text-rendering: optimizeLegibility
}

::-moz-placeholder,
body,
button,
input,
select,
textarea {
	font-family: "Whitney", sans-serif;
	text-rendering: optimizeLegibility
}

::placeholder,
body,
button,
input,
select,
textarea {
	font-family: "Whitney", sans-serif;
	text-rendering: optimizeLegibility
}

a,
button,
div,
input,
label,
select,
span,
strong,
textarea {
	outline: 0
}

button {
	text-decoration: none;
	cursor: pointer;
	-webkit-tap-highlight-color: transparent;
	box-shadow: none;
	border: none
}

.color-Architects {
	color: var(--architects);
}

.color-Analysts {
	color: var(--analysts);
}

.color-Seekers {
	color: var(--seekers);
}

.scroller {
	overflow: auto;
	scrollbar-gutter: stable;
	scrollbar-width: thin;
	scrollbar-color: transparent transparent;
	transition: scrollbar-color 200ms;
}

.scroller:hover {
	/* Transition pour le hover */
	scrollbar-color: var(--scroll-color) transparent;
}

/* Works on Chrome, Edge, and Safari */
.scroller::-webkit-scrollbar {
	width: 6px;
	background-color: transparent;
}
.scroller::-webkit-scrollbar-thumb {
	background-color: transparent;
	border-radius: 10px;
}
.scroller::-webkit-scrollbar-track {
	border-radius: 10px;
}