.stats {
	position: absolute;
	height: 100%;
	right: 0;
	width: 18vw;
	padding: 0 1.5vw;
}

.stats-box {
	height: 100%;
	display: flex;
	flex-direction: column;
}

.stats-box-header {
	display: flex;
	align-items: center;
	margin-bottom: 7vh;
	user-select: none;
}
.stats-box-header-place {
	font-size: 2.5vw;
	font-weight: 500;
	text-transform: uppercase;
  width: 3vw;
  padding-block-end: 2.5vw;
  text-align: center;
	color: var(--text);
  clip-path: polygon(
    calc(2vw / -2 - 5px) 0,
    calc(100% + 2vw / 2 + 5px) 0,
    calc(100% + 2vw / 2 + 5px ) calc(100% - 2vw),
    50% 100%,
    calc(2vw / -2 - 5px) calc(100% - 2vw)
  );
}
.stats-box-header-name {
	flex: 1;
	padding-right: 1vw;
	text-align: center;
	font-size: 2.5vw;
	font-weight: 500;
	text-transform: uppercase;
}

.win {
	color: #43b581
}
.losses {
	color: #f04747
}
.stats-box-body {
	margin-top: 10px;
	color: var(--text);
	padding: 10px;
	margin-bottom: 20px;
	background-color: #2626262a;
	border-radius: 15px;
	filter: saturate(0.9) brightness(0.9);
}
.stats-box-body:hover::-webkit-scrollbar-thumb {
	background-color: var(--scroll-color);
}
.stats-box-body-grid {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	align-items: center;
}
.stats-box-body-grid-type {
	text-align: center;
	font-size: 20px;
	font-weight: 500;
	margin-bottom: 10px;
}
.stats-box-body-grid-icon {
	text-align: center;
}
.stats-box-body-grid-number {
	text-align: center;
	font-size: 19px;
	font-weight: 600;
}