.settings {
	position: absolute;
	z-index: 3;
	overflow: hidden;
	padding: 1.5vw;
}

.settings-button {
	border-radius: 100%;
  border: solid 1px rgba(255, 255, 255, 0.1);
  background-color: #262626;
  filter: saturate(0.9) brightness(0.9);
  transition: filter 200ms;
  padding: 0;
  margin: 0;
  cursor: pointer;
	width: 48px;
  height: 48px;
  transform: none;
}

.settings-button:hover {
	filter: saturate(1) brightness(1.12);
}

.settings-button-effect {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 0;
	overflow: hidden;
	width: 100%;
	height: 100%;
	border-radius: 100%;
}
.settings-button-effect img {
	width: 100%;
	height: 100%;
	border-radius: 50%;
}
.settings-button-effect-blur {
	position: absolute;
	z-index: 1;
	opacity: 0.4;
	filter: blur(10px);
	transform: translateY(10px) scale(1.25);
}
.settings-button-effect-img {
	position: relative;
	z-index: 0;
}

.setting-box {
	width: 190px;
	margin-top: 10px;
	color: var(--text);
	padding: 10px;
	background-color: #26262626;
	border-radius: 15px;
	filter: saturate(0.9) brightness(0.9);
}

.setting-box-player {
	padding: 5px;
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	border-radius: 5px;
	background: #ffffff09;
	transition: background-color 200ms;
}

.setting-box-player:hover {
	background: #ffffff13;
}

.setting-box-player-button {
	background-color: transparent;
	height: 40px;
	height: 40px;
	padding: 0;
  margin: 0;
	user-select: none;
}

.setting-box-player-button-icon {
	margin-left: 5px;
	height: 40px;
	height: 40px;
}
.setting-box-player-text {
	flex: 1;
	text-align: center;
	font-size: 22px;
	font-weight: 500;
}

.setting-box-info {
	margin-top: 10px;
	text-align: center;
	font-size: 18px;
	font-weight: 500;
}